import React from "react";
import "./Line.css";

const Line = ({ points_for_line }) => {
  const lines = points_for_line.map((el, i) => {
    let p = `M ${el[0][0]} ${el[0][1]} L ${el[1][0]} ${el[1][1]}`;
    let color = "rgb(129, 0, 0)";
    let style = "round";
    let k = 1;
    let n;
    if (i === 0) {
      color = "rgb(5, 0, 0)";
      style = "square";
      k = -1;
      n = 3;
    } else if (i === 1) {
      color = "rgb(244, 213, 76)";
      n = 4.5;
    } else if (i === 2) {
      color = "rgb(244, 88, 112)";
      n = 5;
    } else if (i === 3) {
      color = "rgb(203, 102, 165)";
      n = 5;
    } else if (i === 4) {
      color = "rgb(136, 16, 37)";
      n = 4.8;
      k = -1.6;
    } else if (i === 5) {
      color = "rgb(37, 0, 194)";
      n = 3.2;
    } else if (i === 6) {
      color = "rgb(61, 102, 165)";
      n = 4.5;
    } else if (i === 7) {
      color = "rgb(136, 223, 37)";
      k = -0.5;
      n = 4.7;
    } else if (i === 8) {
      color = "rgb(149, 0, 194)";
      n = 4.8;
    } else if (i === 9) {
      color = "rgb(14, 72, 37)";
      n = 3.2;
    } else if (i === 10) {
      color = "rgb(157, 124, 124)";
      n = 3.52;
    } else if (i === 11) {
      color = "rgb(224, 29, 124)";
      n = 3.52;
    } else if (i === 12) {
      color = "rgb(46, 29, 124)";
      n = 2.82;
    } else if (i === 13) {
      color = "rgb(34, 218, 240)";
      n = 5;
    } else if (i === 14) {
      color = "rgb(130, 0, 83)";
      n = 3.8;
    }
    return (
      <g className="dim t" key={i} fill={color} textAnchor="middle">
        <path
          key={i}
          d={p}
          style={{
            stroke: `${color}`,
            fill: "none",
            strokeWidth: `${n}`,
            strokeLinecap: `${style}`,
            strokeDasharray: "1,4"
          }}
        />
        <text
          className="t"
          key={i + 1}
          x={(el[0][0] + el[1][0]) / 2}
          y={(el[0][1] + el[1][1]) / 2}
          dy={k * (Math.random() * 35 + i)}
          dx={k * (Math.random() * 35 + i)}
          fontWeight="bold"
          fontFamily="sans-serif"
          fontSize="20px"
        >
          {el[2]}
        </text>
      </g>
    );
  });
  return lines;
};

export default Line;
