import React, { Component } from "react";
import { SizeMe } from "react-sizeme";
import "./App.css";
import { Window } from "./Component/Window/Window";
import Navbar from "./Component/Navbar/Navbar";
import About from "./Container/About/About";

// import { Route } from "./Component/Window/Window";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      n: 6,
      answer: false
    };
  }
  onEasyLevel = () => {
    this.setState({ n: 4 });
  };

  onMeduimLevel = () => {
    this.setState({ n: 5 });
  };

  onHardLevel = () => {
    this.setState({ n: 6 });
  };

  render() {
    return (
      <div id="all">
        <Navbar />
        <div className="test">
          <SizeMe monitorHeight>
            {({ size }) => (
              <div className="test2 center shadow-5 bw-4 br4 dib">
                <Window
                  size={size}
                  level={this.state.n}
                  onGetLevel={{
                    easy: this.onEasyLevel,
                    medium: this.onMeduimLevel,
                    hard: this.onHardLevel
                  }}
                />
              </div>
            )}
          </SizeMe>
        </div>
        <About />
      </div>
    );
  }
}

export default App;
