import React from "react";
import "./Navbar.css";

const Navbar = () => {
  return (
    <div id="top" className="shadow-5">
      <img
        src={require("../../image/logo.png")}
        alt="logo"
        className="logo"
      ></img>
      <h2 className="grow h2">ไดซก้า</h2>
    </div>
  );
};

export default Navbar;
