import React from "react";
import "./LevelButton.css";

const LevelButton = ({ onGetLevel }) => {
  const { hard } = onGetLevel;
  return (
    <div className="flex" id="btn_div">
      <button
        className="btn grow shadow-1 f6 link dim ba  ph3 pv2 mb2 dib br3 "
        onClick={hard}
      >
        สุ่ม
      </button>
    </div>
  );
};

export default LevelButton;
