import React, { Component } from "react";
import "./CheatButton.css";

class CheatButton extends Component {
  constructor() {
    super();
    this.state = { isClick: false };
  }
  onCheatButt = () => {
    if (this.state.isClick) {
      this.setState({ isClick: false });
    } else {
      this.setState({ isClick: true });
    }
  };

  render() {
    return (
      <div id="cheat">
        <button
          onClick={this.onCheatButt}
          className="cheat grow shadow-1 f6 link  ba  ph3 pv2 mb2 dib br3 bg-lightest-blue"
        >
          ปุ่มแอบดู
        </button>
        {this.state.isClick ? (
          <div>
            <h3 id="ans">
              {this.props.route} <span>ด้วยค่าผ่านทางคือ</span>
              {" " + this.props.min_distance}
            </h3>
          </div>
        ) : (
          ""
        )}
      </div>
    );
  }
}

export default CheatButton;
