function permutator(inputArr) {
  var results = [];
  function permute(arr, memo) {
    var cur,
      memom = memo || [];
    for (var i = 0; i < arr.length; i++) {
      cur = arr.splice(i, 1);
      if (arr.length === 0) {
        results.push(memom.concat(cur));
      }
      permute(arr.slice(), memom.concat(cur));
      arr.splice(i, 0, cur[0]);
    }
    return results;
  }
  return permute(inputArr);
}

export default function allPath(points) {
  let all_path = permutator(points);

  const arr_of_name = points.map((el, i) => `c${i + 1}`);
  let pathName = permutator(arr_of_name);

  all_path = all_path.map((el) => {
    let arr = [];
    for (let j = 0; j < el.length - 1; j++) {
      arr.push([el[j], el[j + 1]]);
    }
    return arr;
  });

  all_path = all_path.map((el) => {
    let arr = [];
    for (let j = 0; j < el.length; j++) {
      let distance = Math.sqrt(
        (el[j][0][0] + el[j][1][[0]]) ** 2 + (el[j][0][1] + el[j][1][1]) ** 2
      );
      distance = Math.round((distance + 15) % 15, 0);
      arr.push(distance);
    }
    return arr;
  });
  let path2 = pathName.map((el, i) => {
    let array = [];
    if (el[0] === "c1" && el[5] === "c6") {
      array.push(el, i);
    }
    return array;
  });

  path2 = path2.filter((el) => {
    if (el.length > 0) return el;
    return "gg";
  });

  all_path = all_path.map((el) => {
    let c = 0;
    for (let k = 0; k < el.length; k++) {
      c += el[k];
    }
    return c;
  });

  const path2Point = path2.filter((el) => {
    return all_path[el[1]];
  });

  const path2Name = path2Point.map((el) => el[0]);
  const path2Value = path2Point.map((el) => all_path[el[1]]);
  // const min_distance = Math.min(...all_path);
  const min2 = Math.min(...path2Value);
  // console.log("p2v", path2Value, path2Name);
  // console.log("min2", min2);

  let index = [];
  for (let i = 0; i < path2Value.length; i++) {
    if (path2Value[i] === min2) {
      index.push(i);
    }
  }

  let min_path_name = [];
  for (let j = 0; j < index.length; j++) {
    min_path_name.push(path2Name[index[j]]);
  }

  const route = min_path_name.join(" หรือ ");

  return [route, min2];

  // return [pathName[index], min_distance];
}
