import React from "react";
import "./Text.css";
const Text = ({ points }) => {
  const text = [];
  for (let i = 0; i < points.length; i++) {
    let k = 0;
    if (i === 2) k = 20;
    else if (i <= 3) k = -80;
    text.push(
      <g key={i} fontSize="15" textAnchor="middle">
        <text
          className="c"
          key={i}
          x={k + points[i][0]}
          y={points[i][1]}
          dy="20"
          dx="30"
        >
          c{i + 1}
        </text>
      </g>
    );
  }

  return text;
};

export default Text;
