import React from "react";
import getCircle from "../../utils/get_circle";
import pointAndLength from "../../utils/points_and_length";
import all_path from "../../utils/all_path";
import Line from "../Line/Line";
import Text from "../Text/Text";
import CheatButton from "../CheatButton/CheatButton";
import LevelButton from "../LevelButton/LevelButton";

import "./Window.css";

const Window = ({ size, level, onGetLevel }) => {
  const { width, height } = size;
  let points = getCircle(level, width, height);
  const co = points;

  const points_for_line = pointAndLength(points);

  let [route, min_distance] = all_path(points);

  points = points.map((el, i) => {
    if (i === 0 || i === 5)
      return <circle key={i} cx={el[0]} cy={el[1]} fill="red" r="12" />;
    return <circle key={i} cx={el[0]} cy={el[1]} fill="rgb(6, 32, 52)" r="7" />;
  });

  route = route.replace(/,/g, " -> ");

  return (
    <div>
      <svg width={width} height={height} id="show">
        <Text points={co} />
        <Line points_for_line={points_for_line} />
        {points}
      </svg>

      <LevelButton onGetLevel={onGetLevel} />
      <CheatButton route={route} min_distance={min_distance} />
    </div>
  );
};

export { Window };
