import React from "react";
import "./About.css";

const About = () => {
  return (
    <div id="about">
      <h2 className="how">วิธีการเล่น</h2>
      <p className="p">
        มีจุดอยู่ 6 จุด คือ c1,c2,...,c6 เลขบนเส้นประคือค่าผ่านทาง ให้เดินทางจาก
        c1 ไปสิ้นสุดที่ c6 โดยผ่านครบทุกจุดที่ไม่ซ้ำกัน
        โดยเงื่อนไขคือต้องเสียค่าเดินทางให้ต่ำที่สุด
      </p>
      <p className="p">มีโอกาสที่จะมีหลายคำตอบนะ !!!ไม่ควรปรับขนาดหน้าจอ!!!</p>

      <div id="credit">
        <h2 className="how">เกี่ยวกับ</h2>
        <img
          src={require("../../image/math.jpg")}
          alt="math"
          className="mathpic"
        ></img>
        <p className="p">
          เกมที่ดูเหมือนจะไม่ยากอะไร แต่การเขียนเป็นโปรแกรมนั้นค่อนข้างซับซ้อน
          และมีคณิตศาสตร์มาเกี่ยวข้องอย่างน่าอัศจรรย์
          และการแสดงผลภาพก็มีคณิตศาสตร์มาคำนวณเพื่อให้จุดต่างๆสามารถอยู่ภายในกรอบนี่กำหนด
          และเว็บนี้ใช้เครื่องมือ React ในการสร้าง ซึ่งผู้สร้างหัดใช้งาน
          จึงเจออุปสรรคที่ทำให้ท้อแท้ใจค่อนข้างมาก
        </p>
        <p className="p">ภาคแมท มหิดล 61</p>
        <p className="p">
          หากเว็บมีปัญหา หรือสอบถามเพิ่มเติม ติดต่อผม (ยูโนะ) 065-930-7037
          ขอบคุณที่เลื่อนลงมาอ่าน
        </p>
      </div>
    </div>
  );
};

export default About;
