export default function getCircle(n, width, height) {
  let w0 = width / 4.5;
  const h0 = height / 3;
  let w = w0;
  if (n === 4) {
    w0 *= 2;
  }
  let h = h0;
  const circle_array = [];
  for (let i = 1; i <= n; i++) {
    const r1 = Math.random() * 1.53;
    const r2 = Math.random() * 1.71;
    if (n >= 5 && i === 2) {
      circle_array.push([w, 3 * (r2 + 5)]);
    } else if (i === 5) {
      circle_array.push([w + r1, height - 30]);
    } else if (n < 6 && i === 4) {
      circle_array.push([w0 * 3, h + r2]);
    } else {
      circle_array.push([w + r1, h + r2]);
    }
    w += w0;
    if (i === 3) {
      w = w0;
      h += h0;
    }
  }
  return circle_array;
}
