export default function pointAndLength(someArray) {
  const pointsAndDistance = [];
  if (someArray) {
    someArray.forEach((el, i) => {
      for (var j = i; j < someArray.length; j++) {
        if (j !== i) {
          let p2 = someArray[j];
          const distance = Math.sqrt(
            (el[0] + p2[0]) ** 2 + (el[1] + p2[1]) ** 2
          );

          pointsAndDistance.push([el, p2, Math.round((distance + 15) % 15)]);
        }
      }
    });
    return pointsAndDistance;
  }
}
